header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #333;
  color: white;
}

header img {
  height: 50px;
  margin-right: 12px;
}

main {
  padding-top: 5vh;
  text-align: center;
  min-height: 75vh;
}

.start-button {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

.start-button:hover {
  background-color: #444;
}

footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #333;
  color: white;
}

.question {
  padding: 16px;
}

.question label:first-of-type {
  display: inline-block;
  color: rgb(6, 6, 41);
  margin-bottom: 8px;
}